header {
  width: 100%;
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
}

body {
  padding-top: 70px;
}

.content {
  text-align: center;
  padding: 20px;
}

.button-container {
  text-align: center;
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  transition: background-color .3s;
  display: none;
}

.load-more:hover {
  background-color: #2980b9;
}

.btn-hidden {
  display: block;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.photo-card {
  height: 265px;
  width: calc(16.6667% - 10px);
  background-color: #f0f8ff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 4px 4px 3px 2px #d3d3d3;
}

.photo-card img {
  height: 70%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  display: flex;
}

